import { required } from "vee-validate/dist/rules";
import {
  extend,
  ValidationObserver,
  ValidationProvider,
  setInteractionMode,
} from "vee-validate";

setInteractionMode("eager");

extend("required", {
  ...required,
  message: "{_field_} can not be empty",
});
import { mapMutations } from 'vuex'

export default {
     auth: false,
  layout: "empty",


      components: {
    ValidationProvider,
    ValidationObserver,
  },


    data(){ 
        return {
            auth:{
                username: null,
                password: null
            },
              show1: false,
      show2: false,
      show3: false,

            snackbar: false,
            text: "",
            timeout: 4000,
            dialog: false,
            isLoading: false
        }
 

    },

    mounted() {

        if(this.$auth.loggedIn){


          if(this.$auth.$state.user.is_teacher == 0){

            this.$router.push('/student');

          }


          if(this.$auth.$state.user.is_teacher == 1){

            this.$router.push('/teacher');

          }

        }


    },
    methods:{
        ...mapMutations(['SET_IS_AUTH', 'SET_API_TOKEN']),

        login(){
    
              this.isLoading = true,
                 
                this.$auth.loginWith('local', {
                    
                    data:{
                        username: this.auth.username,
                        password: this.auth.password
                    },
            
                }).then((response) => {
                    // this.$toast.show('Logging in...')
                    console.log(response.data);
                    this.SET_IS_AUTH(true);
                    // this.SET_API_TOKEN(response.data.access_token);
                    this.$auth.setUser(response.data);
                    this.$auth.setUserToken(response.data.access_token);
                    this.SET_API_TOKEN(this.$auth.$storage._state["_token.local"]);
                    //  this.$auth.fetchUser();
                    console.log(this.$auth);
                    this.isLoading= false;
                  
              
                   if(response.data.is_teacher == 0) {
                   
                    this.$router.push('/student');

                   }

                    if(response.data.is_teacher == 1) {
                   
                    this.$router.push('/teacher');

                   }
                   
                   
                    // console.log("Test !");
                    // console.log(this.$auth.loggedIn);
                    // this.$toast.success('Successfully authenticated')

                    // this.$auth.setUserToken(response.data.access_token)
                    // .then(() => this.$toast.success('User set!'))

                }).catch((error) =>{
                    // this.isLoading = false;
                    // })
                    this.text = error.response.data.message;
                    this.snackbar = true;
                    this.isLoading= false
                    // this.isLoaded = true;
                })

             
        }

    }
}
